@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&display=swap);
.movies .movie {display: -webkit-flex;display: flex; -webkit-align-items: flex-start; align-items: flex-start; -webkit-justify-content: space-between; justify-content: space-between; margin-bottom: 70px; padding: 20px; width: 45%; color: #adaeb9; font-weight: 300; background-color: white; border-radius: 5px; box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);}

.movie img {position: relative; top: -50px; margin-right: 30px; max-width: 150px; width: 100%; box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);}

.movie .movie__title,
.movie .movie__year {margin: 0; font-weight: 300;}

.movie .movie__title {margin-bottom: 5px; color: #2c2c2c; font-size: 24px;}

.movie .movie__genres {display: -webkit-flex;display: flex; margin: 0; margin: 5px 0px; padding: 0; list-style: none;}

.movie__genres li,
.movie .movie__year {margin-right: 10px; font-size: 14px;}

@charset "UTF-8";


* {box-sizing: border-box;}

body {margin: 0; padding: 0; height: 100%; font-family: "Libre Baskerville", serif; background-color: #eff3f7;}

html,
body,
#potato,
.container {display: -webkit-flex;display: flex; -webkit-justify-content: center; justify-content: center; height: 100%;}

.loader {display: -webkit-flex;display: flex; -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center; width: 100%; height: 100%; font-weight: 300;}

.movies {display: -webkit-flex;display: flex; -webkit-justify-content: space-between; justify-content: space-between; -webkit-align-items: flex-start; align-items: flex-start; -webkit-flex-wrap: wrap; flex-wrap: wrap; padding: 50px; padding-top: 70px; width: 80%;}

